module MindfulYoga.Client.MindfulYoga.View

open Fulma
open Fable.React
open Fable.React.Props
open Feliz
open Feliz.Bulma
open MindfulYoga.Client
open SharedViews

let view =
    div [ Class "mindfulyoga"] [
        emptySection
        figure [ Class "image mobile-only"] [
            img [ Src "img/mindful_bg.jpg"]
        ]
        textSection [
            h1 [] [ str "MINDFUL YOGA "; sup [] [str ""]]
            
            inP "Mindful yoga (vmav jga) je praxe jgy s drazem na prost uvdomovn si toho, co se se mnou v ptomnm okamiku dje." 
            inP "Pijmme pocity, emoce, mylenky takov, jak jsou, bez poteby skutenost jakkoliv mnit." 
            inP "Pozorn vnmme kad pohyb, vmme si pocit, emoc, mylenek, dechu... veho co je dominantn. Vytvme prostor, kter nm umouje snze uslyet signly tla a objevit, co opravdu potebujeme, a u na podloce nebo mimo n." 
            inP "Zamen pozornosti na to, co se v danm okamiku dje a jak na probhajc procesy reagujeme, transformuje jgu do formy meditace."
            inP "Vychz z praxe tradin hatha jgy a meditace mindfulness."
            
            h2 [] [ str "Co zskm prax mindful yogy?"]
            [       
                "Sn se mi pociovan stres"
                "Moje tlo se uvoln"
                "Velej vztah k sob  um se pijmat a podporovat" 
                "Zv se mi sebedvra"
                "Um se mt rda a pijmat se" 
                "Propojen se svm tlem"
                "Poznvm, kdo ve skutenosti jsem a co chci"
            ] |> inList |> List.singleton |> div []
            
            inP "Pidej se do skupiny podobn smlejcch jognek."

          
            Html.p [
                prop.className "cta"
                prop.children [
                    Bulma.buttonLink [
                        button.isPrimary
                        button.isMedium
                        prop.target "_blank"
                        prop.href "https://www.facebook.com/groups/mindfulyoga.cz"
                        prop.text "Pidat se ke skupin"
                    ]
                ]
            ]
            
        ]
        
        emptySection
        textSectionCentered [
            h4 [] [ str "\" Ve se odehrv v ptomnm okamiku, jen v nm lze vci mnit. \"" ]
        ]
        
        emptySection
        textSection [
            h2 [] [ str "Jak to vechno funguje?"]
            [       
                "Studenti jsou neustle nabdni k obracen pozornosti k tomu, co provaj v ptomnm okamiku."
                "Pozornost v tle zjiujeme, kde se v tle stres projevuje, samotn uvdomovn si, pomh napt odstranit."
                "Vmme si pocit, emoc, mylenek, smyslovch vjem, ani bychom na n reagovali, pouze z pozice pozorovatele."
                "Znovu se napojme na nae tlo a vytvome si prostor pro vnmn jeho signl. asto jsme od svho tla odpojeni a ijeme jen v hlav. Ume se naslouchat naemu tlu, kde tlo k, e se meme do pozice ponoit hloubji a kde naopak mme zastavit. Pi udren tto pozornosti se nedostvme do bodu, ve kterm uctme bolest  kad vdom pebr zodpovdnost za ten signl vlastnho tla."
                "Vnmme, kde ns omezuje nae mysl. Prozkoumvme svoje hranice."
                "Postupn odhalujeme a uvdomujeme si mylenkov vzorce, tm mme monost si uvdomit, kter nm u neslou a pemnit je."
                "Vnmme, jak vliv m n duevn a emocionln postoj na n fyzick postoj. Zmnou fyzickho postoje v dren tla, dochz i k promn postoj a pocit."
                "Penme klid a vmavost z jgamatky i do bnho ivota."
                
            ] |> inList |> List.singleton |> div []
        ]
    ]
